
























export default {
	data() {
		return {
			userData:[],
			datashow:true,
			bookname:'',
			nowCapName:''
		};
	},
	onShow() {
		this.refreh();
		var value = uni.getStorageSync('reading');
		if(value){
			if(value.bid){
				this.bookname = value.bookname
				this.nowCapName = value.nowCapName
			}else{
				this.bookname = ''
				this.nowCapName = ''
			}
		}else{
			this.bookname = ''
			this.nowCapName = ''
		}
	},
	methods:{
		refreh(){
			this.userData= uni.getStorageSync('user');
			this.datashow = uni.getStorageSync('user').length==0?false:true
		},
		readSelf(id){
			uni.navigateTo({
				url: '../booknote/booknote?bookid=' + id
			});
		},
		godetal(){
			var valu = uni.getStorageSync('reading');
			uni.navigateTo({
				url: '../bookcontenr/bookcontenr?idx=' + valu.nowCap + '&bid=' + valu.bid + '&isread=' + true
			});
		},
		deletData(index){
			var userData = this.userData,
				that = this;
			uni.showModal({
			  title: '提示',
			  content: '确定要删除' + userData[index].title + '吗？',
			  showCancel: true,
			  success: function (res) {
					if (res.confirm) {
						userData.remove(index)
						uni.setStorageSync('user', userData)
						that.refreh()
					} else if (res.cancel) {

					}
			  }
			})
		}
	}
}
/** 
*删除数组指定下标或指定对象 
*/
Array.prototype.remove = function (obj) {
	for (var i = 0; i < this.length; i++) {
		var temp = this[i];
		if (!isNaN(obj)) {
		temp = i;
		}
		if (temp == obj) {
		for (var j = i; j < this.length; j++) {
			this[j] = this[j + 1];
		}
		this.length = this.length - 1;
		}
	}
} 



